<template>
  <div class="users">
    <h1>你好，用户</h1>
    <ul>
      <li v-for="user in users">
          <h2>{{user.name}}</h2>
          <h3>{{user.email}}</h3>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'users',
  props:["users"],
  data () {
    return {
      
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1{
    color:#000
  }
  .users{
    width: 100%;
    max-width: 1200px;
    margin:40px auto;
    padding: 0 20px;
    box-sizing: border-box
  }
  ul{
    display: flex;
    flex-wrap:wrap;
    list-style-type: none;
    padding: 0;
  }
  li{
    flex-grow: 1;
    flex-basis: 300px;
    text-align: center;
    padding: 30px;
    border: 1px solid #222;
    margin: 10px;
    cursor: pointer;
  }
</style>



