<template>
  <div id="app">
    <app-header></app-header>
    <users v-bind:users="users"></users>
    <app-footer></app-footer>
  </div>
</template>

<script>
import Users from './Users'
import Header from './Header'
import Footer from './Footer'

export default {
  name: 'App',
  data() {
    return {
      users:[
      // {name:"Jack",position:"web",show: false},
      // {name:"James",position:"java",show: false},
      // {name:"Kevin",position:"app",show: false}
      ]
    }
  },
  components: {
    "users":Users,
    "app-header":Header,
    "app-footer":Footer
  },
  created(){
    this.$http.get("http://jsonplaceholder.typicode.com/users").then((data) => {
      // console.log(data);
      this.users = data.body;
    })
  }
}
</script>

<style>

</style>



